export default {
  technicalIndicatorExtensions: {},
  shapeExtensions: {},
  addTechnicalIndicatorTemplate (template) {
    if (template) {
      ([].concat(template)).forEach(
        tmp => {
          if (tmp.name) {
            this.technicalIndicatorExtensions[tmp.name] = tmp
          }
        }
      )
    }
  },
  addShapeTemplate (template) {
    if (template) {
      ([].concat(template)).forEach(
        tmp => {
          if (tmp.name) {
            this.shapeExtensions[tmp.name] = tmp
          }
        }
      )
    }
  }
}
