/**
 * 创建一个元素
 * @param name
 * @param styles
 * @return {*}
 */
export function createElement (name, styles = {}) {
  const element = document.createElement(name)
  for (const key in styles) {
    (element.style)[key] = styles[key]
  }
  return element
}
